import { HttpModule } from '@angular/http';
import { Http, Response, RequestOptions, Headers } from '@angular/http';
import { NgModule } from '@angular/core';

declare var jquery:any;
declare var $ :any;
declare var require :any;

@NgModule({
	imports: [
		HttpModule
	]
})


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
})
export class AppComponent implements OnInit{
  @ViewChild('initBlock') initBlock: ElementRef;
  @ViewChild('splashScreen') splashScreen: ElementRef;
  @ViewChild('textArea') textArea: ElementRef;
  @ViewChild('responseArea') responseArea: ElementRef;
  @ViewChild('loader') loader: ElementRef;
  @ViewChild('responseAreaText') responseAreaText: ElementRef;
  @ViewChild('modal') modal: ElementRef;
  @ViewChild('modalClose') modalClose: ElementRef;
  @ViewChild('copyButton') copyButton: ElementRef;
  @ViewChild('neoscanBalances') neoscanBalances: ElementRef;
  @ViewChild('wikiList') wikiList: ElementRef;

  constructor(private http: Http) { }

  netWorth;
  balances;
  wikilist;
  title = 'neo-architect';
  initMsg = "";
  query = "";
  response = "";
  responseData;
  lastIntent;
  lastQuery = "";


  valueQuantity;
  confidenceQuantity;
  valueUnit;
  confidenceUnit;
  valueRecipient;
  confidenceRecipient;
  valueDate;
  confidenceDate;
  stageFinal = false;

  clearData() {  
  this.valueQuantity = null;
  this.confidenceQuantity = null;
  this.valueUnit = null;
  this.confidenceUnit = null;
  this.valueRecipient = null;
  this.confidenceRecipient = null;
  this.valueDate = null;
  this.confidenceDate = null;
  this.stageFinal = false;
  if (this.lastIntent != null) this.lastIntent.value = null;
  if (this.lastIntent != null) this.lastIntent.confidence = null;
  }

  composeResponse(responseData) {

  	$('.responseAreaText').css('fontSize', 60);
  	var responses = require('./responses.json');
  	var page = require('./pages/NEOpage.html');

    var confidence,value,intents;

    if (this.stageFinal == true && this.lastIntent != null && this.lastIntent.value === "send_token") 
    {
      if (responseData.agreement == null) {
        value = this.lastIntent.value;
        confidence = this.lastIntent.confidence;
      }
      else if (responseData.agreement[0].value === "yes") 
        { this.clearData(); this.loaderHide(); return "Transaction successful."; }
      else if (responseData.agreement[0].value === "no") 
        { this.clearData(); this.loaderHide(); return "Transaction cancelled."; }
    }
  	else if ((responseData.intent == null || responseData.intent[0].value === "token_description" || responseData.intent[0].value === "check_token") && this.lastIntent != null && this.lastIntent.value === "send_token") 
    {
      value = this.lastIntent.value;
      confidence = this.lastIntent.confidence;
    }
  	else if (responseData.intent == null) {
  		value = "token_description";
      confidence = 1;
	  }
    else {
      confidence = responseData.intent[0].confidence;
      value = responseData.intent[0].value;
      intents = responseData.intent;
      this.clearData();
      this.lastIntent = responseData.intent[0];
      }
  	
    switch (value) {
      case "wiki_search":
        this.loaderShow();
        this.neoSearch();
        return "";
      case "token_description":
        $('.loader').hide();
        return this.tokenDescription(responseData);
      case "token_conversion":
        return this.tokenConversion(responseData);
      case "check_token":
        return this.checkToken(responseData);
      case "buy_token":
        $('.loader').hide(); 
        return this.buyToken(responseData);
      case "sell_token":
        $('.loader').hide();
        return this.buyToken(responseData);
      case "send_token":
        $('.loader').hide();
        return this.sendToken(responseData);
      case "neo_eth":
        this.loaderHide();
        var page = require('./pages/NeoEth.html');
        return page;
      case "neo_contract":
        $('.loader').hide();
        var page = require('./pages/NeoContract.html');
        return page;
  		default:
      $('.loader').hide();
			var responseList = responses[value];
		  	var response = responseList[Math.floor(Math.random() * responseList.length)];	
  			return response;
  	}
  }

  fetchUndefined(){
    var responses = require('./responses.json');
    
    var responseList = responses['undefined'];
    var response = responseList[Math.floor(Math.random() * responseList.length)];  
    $('.loader').hide();
    return response;
  }

  neoSearch(){

  var wikiUrl = "https://smarteconomy.wiki/api.php?origin=https://archie-neo.github.io";
  //var neonewsUrl = "https://neonewstoday.com/?s="+this.lastQuery; no API yet

  this.http.get(wikiUrl,{

      params: {
        origin: '*',
        action: 'query',
        list: 'search',
        srwhat: 'text',
        format: 'json',
        srsearch: this.lastQuery
        }}).subscribe(res => { 


        this.wikilist = res.json().query.search;
        
        if (this.wikilist.length == 0) return this.showResponse(this.fetchUndefined());

        for (var _i = 0; _i < this.wikilist.length; _i++)
        {
          var cleanText = this.wikilist[_i].snippet.replace(/<\/?[^>]+(>|$)/g, "");
          this.wikilist[_i].snippet = cleanText;

          this.wikilist[_i].link = "https://smarteconomy.wiki/"+this.wikilist[_i].title;
        }
        
        this.wikiList.nativeElement.style.visibility="visible";
        this.showResponse("");
        this.loaderHide();
        });

    return "";
  }

  tokenDescription(responseData) 
  {
    var valueUnit, confidenceUnit, valueUnit2, confidenceUnit2;
    var valueQuantity, confidenceQuantity;

    if (responseData.currency_type != null)
    {
      valueUnit = responseData.currency_type[0].value;
      confidenceUnit = responseData.currency_type[0].confidence;

      if (confidenceUnit < 0.75) return this.neoSearch();
    }
    else return this.neoSearch();

    if (responseData.currency_type[1] != null)
    {
      valueUnit2 = responseData.currency_type[1].value;
      confidenceUnit2 = responseData.currency_type[1].confidence;

      if (valueUnit === "NEO" && confidenceUnit2 > 0.75){
        valueUnit = valueUnit2;
        confidenceUnit = confidenceUnit2;
      }

    }

    if (responseData.number != null)
    {
      valueQuantity = responseData.number[0].value;
      confidenceQuantity = responseData.number[0].confidence;
    }

    if (valueQuantity == null)
    {


    try
    {
      var pagename = './pages/'+valueUnit+'page.html';
      var page = require(""+pagename+"");
      return page;
    }
    catch(e) {
       return this.neoSearch();
    }

    }
    else {
      this.checkToken(responseData);
    }

    return "";
  }

  tokenConversion(responseData)
  {
    var valueUnit, confidenceUnit;
    var valueUnit2, confidenceUnit2;

    var valueQuantity, confidenceQuantity;

    if (responseData.currency_type != null)
    {
      valueUnit = responseData.currency_type[0].value;
      confidenceUnit = responseData.currency_type[0].confidence;
    }
    else return this.fetchUndefined();

    if (responseData.currency_type[1] != null)
    {
      valueUnit2 = responseData.currency_type[1].value;
      confidenceUnit2 = responseData.currency_type[1].confidence;
    }
    else return this.fetchUndefined();

    if (responseData.number != null)
    {
      valueQuantity = responseData.number[0].value;
      confidenceQuantity = responseData.number[0].confidence;
    }
    else valueQuantity = 1;

    if (valueUnit == null || confidenceUnit < 0.8 || confidenceUnit2 < 0.8) {
      return this.fetchUndefined();
    }

    var url = 'https://min-api.cryptocompare.com/data/price';

    this.http.get(url, {
      params: {
        fsym: valueUnit,
        tsyms: valueUnit2,
        extraParams: 'neo_architect'
      }}).subscribe(res => { 

        
        var conversionRate = res.json()[valueUnit2];
     
        var totalPrice = Number(conversionRate) * valueQuantity;
        $('.loader').hide();

        this.showResponse(valueQuantity+" "+valueUnit+" = "+totalPrice+" "+valueUnit2);
        });

      return "";
  }

  checkToken(responseData)
  {
    var valueUnit, confidenceUnit;
    var valueQuantity, confidenceQuantity;

    if (responseData.currency_type != null)
    {
      valueUnit = responseData.currency_type[0].value;
      confidenceUnit = responseData.currency_type[0].confidence;
    }

    if (responseData.number != null)
    {
      valueQuantity = responseData.number[0].value;
      confidenceQuantity = responseData.number[0].confidence;
    }

    if (valueUnit == null || confidenceUnit < 0.8) {
      return this.fetchUndefined();
    }

    var url = 'https://min-api.cryptocompare.com/data/pricemultifull';

    this.http.get(url, {
      params: {
        fsyms: valueUnit,
        tsyms: 'USD',
        extraParams: 'neo_architect'
      }}).subscribe(res => { 

      
        try {
        var tokenDisplayData = res.json().DISPLAY[valueUnit].USD;
        var tokenRawData = res.json().RAW[valueUnit].USD;

        this.loaderHide();
        this.composeTokenOverview(tokenDisplayData,tokenRawData,valueQuantity);}
        catch {
          this.loaderHide();
          return this.showResponse("I am sorry, but I am unable to fetch info for this.");
        }
        });

      return "";
  }

  loaderShow(){
    $('.loader').show();
  }

  loaderHide(){
    $('.loader').hide();
  }

  composeTokenOverview(tokenDisplayData,tokenRawData,quantity)
  {

    var totalPrice;

    if (quantity != null)
    {
      totalPrice = quantity+" "+tokenDisplayData.FROMSYMBOL+" = $ "+quantity * Number(tokenRawData.PRICE)+"<hr>";
    }

    var mcap = "<br>Market Cap: "+tokenDisplayData.MKTCAP;
    var price = "Price of the token: "+tokenDisplayData.PRICE;
    var supply = "<br>Supply: "+tokenDisplayData.SUPPLY;
    
    var changePercent = Number(tokenRawData.CHANGEPCT24HOUR).toFixed(2);
    var change24 = "<br>24 Hours change: "+changePercent+"%";

    var result;
    if (quantity == null) result = tokenRawData.FROMSYMBOL+"<hr>"+price+change24+mcap+supply;
    else result = totalPrice+price+change24+mcap+supply;

    this.showResponse(result);
  }

  buyToken(responseData){
   
    var valueUnit, confidenceUnit;

    if (responseData.currency_type != null)
    {
      valueUnit = responseData.currency_type[0].value;
      confidenceUnit = responseData.currency_type[0].confidence;
    }

    if (valueUnit == null || confidenceUnit < 0.8) {
      return this.fetchUndefined();
    }

    return "To buy and sell "+valueUnit+" tokens, you can visit Switcheo exchange or check Coinmarketcap for all exchanges this token is listed on. <hr> Switcheo exchange: <a href='https://switcheo.exchange/markets/"+valueUnit+"_NEO' target='_blank'>switcheo.exchange</a><br> Coinmarketcap: <a href='https://coinmarketcap.com/search/?q="+valueUnit+"' target='_blank'>coinmarketcap.com</a>";
  }
  sendToken(responseData){
  
    if (this.valueUnit == null && responseData.currency_type != null)
    {
      this.confidenceUnit = responseData.currency_type[0].confidence;

      if (this.confidenceUnit < 0.8) return "I don't know any token by that name. Please enter correct token name.";
      else this.valueUnit = responseData.currency_type[0].value;
    }

    if (this.valueQuantity == null && responseData.number != null) 
    {
      this.confidenceQuantity = responseData.number[0].confidence;

      if (this.confidenceQuantity < 0.8) return "You entered incorrect quantity. Please enter correct one.";
      else this.valueQuantity = responseData.number[0].value;
    }

    if (this.valueRecipient == null && responseData.contact != null)
    {
      this.confidenceRecipient = responseData.contact[0].confidence;

      if (this.confidenceRecipient < 0.8) return "You entered incorrect recipient. Please enter correct one."
      else this.valueRecipient = responseData.contact[0].value;
    }

    if (this.valueDate == null && responseData.datetime != null) 
    {
      this.confidenceDate = responseData.datetime[0].confidence;
    
      if (this.confidenceDate < 0.8) return "You entered incorrect date. Please enter correct one.";
      else if (responseData.datetime[0].value != null) this.valueDate = responseData.datetime[0].value.split(".")[0];
      else this.valueDate = responseData.datetime[0].from.value.split(".")[0];
    }

    if (this.valueUnit == null) {
      return "Which NEP5 token do you want to send?"
    }

    if (this.valueQuantity == null) {
      return "How many tokens do you want to send?"
    }

    if (this.valueRecipient == null) {
      return "To whom do you plan to send these tokens?"
    }

    if (this.valueDate == null) {
      return "What time do you want to plan transaction?"
    }

    this.stageFinal = true;
    return "Summary:<br> Quantity: "+this.valueQuantity+"<br> Unit: "+this.valueUnit+"<br> Recipient: "+this.valueRecipient+"<br> Time: "+this.valueDate+"<hr> Do you confirm this transaction?";
  }

  resize_to_fit_NeoScan() {
  var fontsize = $('.neoscanBalances').css('font-size');
  $('.neoscanBalances').css('fontSize', parseFloat(fontsize) - 1);

  if ($('.neoscanBalances').height() >= $('.responseArea').height()) {
    this.resize_to_fit_NeoScan();
    }
  }

  resize_to_fit() {
  var fontsize = $('.responseAreaText').css('font-size');
  $('.responseAreaText').css('fontSize', parseFloat(fontsize) - 1);

  if ($('.responseAreaText').height() >= $('.responseArea').height()) {
    this.resize_to_fit();
  	}
  }

  processNeoScanAddress(address){

    this.netWorth = 0;
    this.loaderShow();
    let url = 'https://neoscan.io/api/main_net/v1/get_balance/'+address;
    
      this.http.get(url, {
      params: {
      }}).subscribe(res => { 
                  
                   this.balances = res.json().balance;

                   for (var _i = 0; _i < this.balances.length; _i++) {

                    var token = this.balances[_i];
                    if (token.amount == 0)
                    {
                      this.balances.splice(_i,1);
                      _i = _i - 1;
                    }
                    else {
                      this.balanceUsd(token);
                    }
                   }

                   this.showResponse("");
                   this.loaderHide();
                   this.neoscanBalances.nativeElement.style.visibility="visible";
                   
                  },
                  err => {
                    this.showResponse("Address you entered is invalid. <br>Please correct it.");
                    this.loaderHide();
                  }
                  );
  }

  balanceUsd(token){

    var unit = token.asset;
    var amount = token.amount;

    let url = 'https://api.wit.ai/message';
    var question = unit;
    var tokenWit = "YE7TQH244HU7XFKGCLC2TGPQNBZ73UXU";

    this.http.get(url, {
      params: {
        q: question,
        access_token: tokenWit
      }}).subscribe(res => { 

                   this.responseData = res.json().entities;
                   
                   var valueUnit, confidenceUnit;

                   if (this.responseData.currency_type != null)
                   {
                      valueUnit = this.responseData.currency_type[0].value;
                      confidenceUnit = this.responseData.currency_type[0].confidence;

                      var url = 'https://min-api.cryptocompare.com/data/price';

                      this.http.get(url, {
                        params: {
                          fsym: valueUnit,
                          tsyms: 'USD',
                          extraParams: 'neo_architect'
                        }}).subscribe(res => { 

                          var price = res.json().USD;
                          var priceTotal = token.amount * price;

                          if (priceTotal.toString() === "NaN" || priceTotal == 0) {
                            token.usd = "";
                          }
                          else {
                          token.usd = "($ "+priceTotal.toFixed(2)+")";
                          this.netWorth = this.netWorth + priceTotal;
                          }

                          });                        
                      return;
                   }
                   else
                   {
                      token.usd = "";
                      return;
                   }

                   });
    return "calculating...";
  }

  processNeoScanTransaction(tx){
    $('.responseAreaText').css('fontSize', 60);
    this.showResponse("This is transaction. <br> Detailed preview coming soon.");    
  }

  determineQueryType(question)
  {

    if (question === "") return;
    else if (question === "AbiRBgNjHd3xvxQpQjHEugVynHLjZdW4Pl") {
        $('.responseAreaText').css('fontSize', 60);
        this.showResponse("Just Do It.");
        return 1;
      }
    else if (question.length == 34 && question.indexOf(' ') == -1 && question.indexOf('A') == 0){
        this.processNeoScanAddress(question);
        return 1;
    }
    else if (question.length == 64 && question.indexOf(' ') == -1 ){
        this.processNeoScanTransaction(question);
        return 1;
    }

    return 0;
  }

  doPOST(query) {
  		let url = 'https://api.wit.ai/message';
  		var question = query;
  		var token = "YE7TQH244HU7XFKGCLC2TGPQNBZ73UXU"

      if (this.determineQueryType(question)) return;

  		$('.loader').show();
  		this.http.get(url, {
      params: {
        q: question,
        access_token: token
      }}).subscribe(res => { 
                   this.responseData = res.json().entities;
                   this.showResponse(this.composeResponse(this.responseData));
                   this.determineSentiment(this.responseData);
      						 //this.responseAreaText.nativeElement.innerHTML = this.composeResponse(this.responseData);
      						 //this.resize_to_fit();
      						});
  }

  determineSentiment(responseData)
  {
    var valueSentiment;
    var confidenceSentiment;
    var valueIntent;
    var confidenceIntent;

    if (responseData.sentiment != null) 
    {
      valueSentiment = responseData.sentiment[0].value;
      confidenceSentiment = responseData.sentiment[0].confidence;
    }

    if (responseData.intent != null)
    {
      valueIntent = responseData.intent[0].value;
      confidenceIntent = responseData.intent[0].confidence;
    }
    else return;


    if (valueIntent === "architect_insult") {
      $('.textArea').css('caret-color','white');
      $('.responseAreaText').css('color', 'white');
      $('.fullscreenDiv').css('background','rgba(231, 76, 60,1.0)');
      $('.fullscreenDiv').css('background-color','rgba(231, 76, 60,1.0)');
      $('.responseArea').addClass('angryBackground');
      $('.loader').css('border','3px solid white');
      $('.loader').css('border-top','3px solid rgba(231, 76, 60,1.0)');
      $('.donate').css('color','white');

    }
    else if (valueIntent === "architect_apology") {
      $('.textArea').css('caret-color','rgba(46, 204, 113,1.0)');
      $('.responseAreaText').css('color', 'rgba(46, 204, 113,1.0)');
      $('.fullscreenDiv').css('background-image','linear-gradient(to top, #dfe9f3 0%, white 100%)');
      $('.loader').css('border','3px solid rgba(46, 204, 113,1.0)');
      $('.loader').css('border-top','3px solid white');
      $('.donate').css('color','rgba(46, 204, 113,1.0)');
      $('.responseArea').removeClass('angryBackground');
    }

  }

  showResponse(response) {

    
    if (!(response instanceof Array))
    {
      this.responseAreaText.nativeElement.innerHTML = response;
      this.resize_to_fit();
    }
    else
    {
      response.forEach((item, index) => {

        setTimeout(() => 
      {
         this.responseAreaText.nativeElement.innerHTML = response[index];
         this.resize_to_fit();
      },
      1200*index);
      });
    }
   
  }

  copyToClipboard(event){

    if (this.copyButton.nativeElement.style.backgroundColor != "rgb(46, 204, 113)") {
      this.copyButton.nativeElement.style.backgroundColor = "rgb(46, 204, 113)";
    }
    else {
     this.copyButton.nativeElement.style.backgroundColor = ""; 
    }
  }

  showHelp(event){
    this.modal.nativeElement.style.display = "block";
  }
  hideHelp(event){
    this.modal.nativeElement.style.display = "none";
  }
  checkHelp(event){

   
    if (event.target == this.modal.nativeElement) {
      this.modal.nativeElement.style.display = "none";
    }
  }


  ngOnInit() {

  	this.initialize();
  }

  onClicked(event) {
  }

  onKeydown(event) {
  	if (event.key === "Enter") {

      event.preventDefault();
      this.balances = null;
      this.neoscanBalances.nativeElement.style.visibility="hidden";
      this.wikiList.nativeElement.style.visibility="hidden";

      if ($('.textArea').val() !== "") {
        if ($('.textArea').val().trim() === "") return; 
      }

      if (this.lastQuery != null && $('.textArea').val() == "")
      {
        this.doPOST(this.lastQuery);
      } 
      else 
      {
        this.lastQuery = $('.textArea').val();
        this.doPOST(this.lastQuery);
        $('.textArea').attr("placeholder", $('.textArea').val());
        $('.textArea').val("");  
      }  
  		    
    }
  }

  initialize() {
    this.neoscanBalances.nativeElement.style.visibility="hidden";
    this.wikiList.nativeElement.style.visibility="hidden";
    this.netWorth = 0;

  //	this.initMsg = "Initializing core modules";

  //	setTimeout(() => 
	//{
  //  	this.initMsg = "Waking Up";
	//},
	//2000);

	//setTimeout(() => 
	//{
  //  	this.initMsg = "Meet the Architect";
  //  	this.initBlock.nativeElement.classList.add('loading');
  //  	this.initBlock.nativeElement.classList.remove('loading-pulsate');
  //  	this.initBlock.nativeElement.style["font-size"] = "60px";

	//},
	//2000);  	

      this.initMsg = "Meet the Architect";
      this.initBlock.nativeElement.classList.add('loading');
      this.initBlock.nativeElement.classList.remove('loading-pulsate');
      this.initBlock.nativeElement.style["font-size"] = "60px";


	setTimeout(() => 
	{
    	this.initMsg = "";
    	this.splashScreen.nativeElement.style.display = "none";
    	this.textArea.nativeElement.focus();
    	this.responseAreaText.nativeElement.innerHTML = "Hi, I am the Architect, you can ask me anything you want about NEO and I will answer as best as I can.";
      this.resize_to_fit();
	},
	1000);

  }

}
